/** Menu **/
.nav {
  list-style-type: none;
  padding: 0;
  font-family: "Fira Sans Condensed", sans-serif; }
  @media only screen and (max-width: 769px) {
    .nav {
      display: none;
      align-items: center;
      flex-direction: column; } }
  @media only screen and (min-width: 770px) {
    .nav {
      display: flex;
      flex-direction: row;
      border-top: 2px solid #eee;
      margin: 10px 0 50px 0;
      font-size: 1.6rem;
      vertical-align: middle;
      justify-content: center;
      align-items: start;
      position: relative;
      top: -1rem; } }
  .nav li {
    text-transform: uppercase; }
    @media only screen and (min-width: 770px) {
      .nav li {
        max-width: 180px;
        display: inline-block;
        padding: 0 20px 0 20px; } }
    @media only screen and (max-width: 769px) {
      .nav li {
        padding: 1rem;
        border-bottom: 1px solid #E1E8ED; } }
  .nav li:last-child {
    border-bottom: 0; }

@media only screen and (max-width: 769px) {
  .nav.mobile-active {
    display: flex; } }

.nav-item {
  background-color: #FFFFFF; }
  @media only screen and (min-width: 770px) {
    .nav-item {
      margin: -1rem auto 0 auto;
      border-right: 1px solid #E1E8ED;
      text-align: center; } }
  .nav-item a {
    color: #66757F; }

.nav-item:last-child {
  border-right: none; }

.nav-item-active {
  color: #000;
  text-decoration: none;
  font-weight: bold; }
  .nav-item-active a {
    color: #292F33; }
    @media only screen and (min-width: 770px) {
      .nav-item-active a {
        border-bottom: 2px solid #E1E8ED; } }

/** Nested child submenu **/
.submenu {
  display: none;
  position: absolute; }

.submenu-active {
  display: block;
  background-color: #CCD6DD;
  padding: 1rem; }

.submenu-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center; }

@media only screen and (max-width: 769px) {
  .mobile-menu-button {
    display: block; } }

@media only screen and (min-width: 770px) {
  .mobile-menu-button {
    display: none; } }

.header-title {
  flex: 1 1 100%;
  text-align: center;
  font-family: "Baskerville", serif; }
  @media only screen and (max-width: 769px) {
    .header-title {
      font-size: 5rem;
      margin: 0 auto 20px auto; } }
  @media only screen and (min-width: 770px) {
    .header-title {
      font-size: 10rem;
      margin: 0 auto 40px auto; } }

.header-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-drection: column;
  align-items: center; }
  @media only screen and (max-width: 769px) {
    .header-container {
      margin: 0 auto 20px auto; } }
  @media only screen and (min-width: 770px) {
    .header-container {
      margin: 0 auto 40px auto; } }

@media only screen and (min-width: 770px) {
  nav {
    flex-direction: row; } }

.hero-container {
  margin: 0 auto; }
  .hero-container > img {
    width: 100%; }

.wedding-party-member {
